export default {
    /**
     * 
     * @param {} params 
     * @returns 
     */
    fetchAllCodepackageData(params) {
        return IDM.http.get(IDM.setting.api.allCodepackageUrl || IDM.setting.mockurl.allCodepackageUrl, params);
    },
    /**
     * md
     * @param {
     * mdPath:md
     * type:codemodule
     * moduleClassName:
     * mdContent:md
     * } params 
     * @returns 
     */
     saveMdEditorData(params){
        return IDM.http.post(IDM.setting.document.saveMarkDownFileUrl, params)
    }
}