<template>
  <!---->
  <div class="idmcore_document">
    <div class="idm_top_toolbar">
      <div class="idm_top_toolbar_left">
        <idm-a-popover overlayClassName="idm_popover_codepackage_choose_outbox" @visibleChange="codepackageVisibleToggle" placement="bottomLeft">
          <template slot="content">
            <div class="idm_popover_codepackage_choose">
              <ul class="idm_codepackage_list_box scrollbar_style">
                <li v-for="(item,index) in allCodepackageData" @click="checkCodepackage(item)" :key="index" :class="{'active':item.active}">
                  <span>{{item.className}}</span><label>{{item.versionCount}}</label>
                </li>
              </ul>
              <template v-for="(item,index) in allCodepackageData">
                <ul :key="index" v-if="item.active" class="idm_codepackage_version_box scrollbar_style">
                  <li v-for="(sitem,sindex) in item.versionList" @click="checkVersion(item,sitem)" :class="{'active':item.className==checkVersionObject.className&&sitem.version==checkVersionObject.version}" :key="sindex">
                    <div class="version_title_box">
                      <span>{{sitem.version}}</span><label>{{sitem.author}}</label>
                    </div>
                    <div class="version_desc">{{sitem.remark||sitem.lasttime}}</div>
                  </li>
                </ul>
              </template>
            </div>
          </template>
          <div>
            <svg-icon icon-class="switch-org-icon" class="switch-icon"/>
            <span class="checked_codepackage_name">{{checkVersionObject.className||''}}</span>
            <label class="checked_codepackage_version">{{checkVersionObject.version}}</label>
            <idm-a-icon type="down" class="ant_icon_down"/>
          </div>
        </idm-a-popover>
      </div>
      <div class="idm_top_toolbar_right">
        <div @click="downloadMdFile"></div>
        <div>
          <idm-a-tooltip title=" Ctrl+S">
            <div class="savebtn" @click="saveMdEditorContent"> </div>
          </idm-a-tooltip>
        </div>
      </div>
    </div>
    <div class="idm_content_box" :class="{'show_left':showLeftModuleList}">
      <div class="idm_cb_module_left">
        <div class="idm_cb_module_left_title">
          <span @click="showLeftModuleList=true"></span>
          <idm-a-icon type="left" @click="showLeftModuleList=false"/>
        </div>
        <div class="idm_cb_module_left_search">
          <idm-a-input-search placeholder="" v-model="serachModuleText" />
        </div>
        <div class="idm_cb_module_left_list scrollbar_style">
          <div @click="changeModuleActive()" v-show="checkVersionObject.version" class="index_doc" :class="{'active':!checkModuleObject.classId}">
            <div class="item_title"><span></span></div>
          </div>
          <div @click="changeModuleActive(module)" :class="{'active':checkModuleObject.classId==module.classId}" v-for="(module,index) in checkVersionObject.configObject.module" v-show="!serachModuleText||module.comName.indexOf(serachModuleText)>-1||module.className.indexOf(serachModuleText)>-1" :key="index">
            <div class="item_title"><span :title="module.comName">{{module.comName}}</span><label :title="module.className">{{module.className}}</label></div>
            <div class="item_desc" :title="module.classId">{{module.classId}}</div>
          </div>
        </div>
      </div>
      <div class="idm_cb_module_editor">
        <v-md-editor v-model="editorText" @save="saveMdEditorContent" @change="mdEditorChange" :include-level="[1,2,3,4,5,6]" :default-show-toc="true" height="calc(100vh - 60px)" style="width:100%;"></v-md-editor>
      </div>
    </div>
  </div>
</template>

<script>
import "@/plugins/document";
export default {
  name: "Document",
  components: {},
  data() {
    return {
      //
      attrCachePool:IDM.cachePool(500),
      //text
      editorText: '',
      //
      editorIsChange:false,
      //
      showLeftModuleList:true,
      //
      allCodepackageData:[],
      //
      serachModuleText:"",
      //
      checkVersionObject:{
        configObject:{}
      },
      //
      checkModuleObject:{

      },
      //markdown
      codepackageMDTemp:"",
      //markdown
      moduleMDTemp:""
    };
  },
  mounted() {
    this.initData();
    this.initTempData();
  },
  created() {
    // this.moduleId = this.$route.params.moduleId;
  },
  methods: {
    /**
     * 
     */
    windowResizeEvent(callback) {
        window.onresize = function() {
            var target = this;
            if (target.resizeFlag) {
                clearTimeout(target.resizeFlag);
            }
            target.resizeFlag = setTimeout(function() {
                callback();
                target.resizeFlag = null;
            }, 100);
        }
    },
    //
    initData(){
      const that = this;
      this.$documentApi.fetchAllCodepackageData().then((res) => {
         if (
              !res.data ||
              (res.data && _.has(res.data, "code") && !res.data.data)
            ) {
              return;
            }
            res.data.data.forEach(item=>item.active=false);
            that.allCodepackageData = res.data.data;
      })
      .catch(function (error) {
        console.log(error);
      });

    },
    initTempData(){
      const that = this;
      this.getMdContent("$doctemp/codepackageMDTemp.md",function(res){
        if(res&&res.data){
          that.codepackageMDTemp = res.data;
        }
      })
      this.getMdContent("$doctemp/moduleMDTemp.md",function(res){
        if(res&&res.data){
          that.moduleMDTemp = res.data;
        }
      })
    },
    //
    checkCodepackage(item){
      this.allCodepackageData.forEach(item=>item.active=false);
      item.active = true;
    },
    //
    checkVersion(fitem,item){
      if(this.editorText&&this.editorIsChange&&!confirm("")){
        return;
      }
      this.checkVersionObject = item;
      console.log(" ~ file: Document.vue ~ line 129 ~ checkVersion ~ item", item)
      this.initCodepackageMd();
    },
    initCodepackageMd(){
      const that = this;
      this.getMdContent(this.getModuleAbsoluteDir()+this.checkVersionObject.codePath+IDM.setting.document.codepackageDocumentPath+"index.md",function(res){
        that.editorText=res?res.data:"";
        if(!that.editorText){
          var objectData = {
            ...that.checkVersionObject
          }
          let moduleListStr = "";
          that.checkVersionObject.configObject.module.forEach((item,index)=>{
            moduleListStr+=`${index+1}. ${item.comName}${item.className}${item.classId}\n`;
          })
          objectData.moduleList=moduleListStr;
          
          that.editorText = that.$replace(that.codepackageMDTemp, objectData);
        }
        setTimeout(() => {
          that.editorIsChange = false;
        }, 100);
      })
    },
    //
    codepackageVisibleToggle(visible){
      if(!visible){
        this.allCodepackageData.forEach(item=>item.active=item.className==this.checkVersionObject.className);
      }
    },
    /**
     * 
     */
    async changeModuleActive(module){
      if(this.editorText&&this.editorIsChange&&!confirm("")){
        return;
      }
      const that = this;
      this.checkModuleObject = module||{};
      console.log(" ~ file: Document.vue ~ line 190 ~ changeModuleActive ~ this.checkModuleObject", this.checkModuleObject)
      if(!module){
        this.initCodepackageMd();
        return;
      }

      let attrJsonSrc = this.checkVersionObject.codePath;
      if(this.checkModuleObject.className){
        attrJsonSrc = attrJsonSrc+"static/"+IDM.setting.applications.moduleAttrFolderName+"/"+this.checkModuleObject.className+".json";
      }
      //
      let attrCache = that.attrCachePool.get(that.checkVersionObject.projectNo+":"+attrJsonSrc);
      if(!attrCache){
        await IDM.http.get(IDM.url.getWebPath("@"+attrJsonSrc,false,that.checkVersionObject.projectNo))
            .then((res) => {
              if(res&&res.data&&res.data.compositeAttr){
                that.checkModuleObject.compositeAttr = res.data.compositeAttr;
              }
              if(res&&res.data&&res.data.innerAttr){
                that.checkModuleObject.innerAttr = res.data.innerAttr;
              }
              that.attrCachePool.put(that.checkVersionObject.projectNo+":"+attrJsonSrc,res);
            })
            .catch(function (error) {
              IDM.message.error(""+attrJsonSrc+"message"+error.message);
            });
      }else{
          if(attrCache&&attrCache.data&&attrCache.data.compositeAttr){
            that.checkModuleObject.compositeAttr = attrCache.data.compositeAttr;
          }
          if(attrCache&&attrCache.data&&attrCache.data.innerAttr){
            that.checkModuleObject.innerAttr = attrCache.data.innerAttr;
          }
      }

      this.getMdContent(this.getModuleAbsoluteDir()+this.checkVersionObject.codePath+IDM.setting.document.moduleDocumentPath+this.checkModuleObject.className+".md",function(res){
        that.editorText=res?res.data:"";
        if(!that.editorText){
          var objectData = {
            ...that.checkModuleObject
          }
          objectData.versionObject=that.checkVersionObject;
          //
          let moduleAttrList = [];
          that.checkModuleObject.compositeAttr&&that.checkModuleObject.compositeAttr.forEach(item=>{
            moduleAttrList.push("### "+item.text+(item.type=="group"||item.type=="inlineGroup"?"":((item.type=="table"?"":"")+""+item.bindKey+"")))
            item.children&&item.children.forEach(item1=>{
              moduleAttrList.push("#### "+item1.text+(item1.type=="group"||item1.type=="inlineGroup"?"":((item1.type=="table"?"":"")+""+item1.bindKey+"")))
              item1.children&&item1.children.forEach(item2=>{
                moduleAttrList.push("##### "+item2.text+(item2.type=="group"||item2.type=="inlineGroup"?"":((item2.type=="table"?"":"")+""+item2.bindKey+"")))
                item2.children&&item2.children.forEach(item3=>{
                  moduleAttrList.push("###### "+item3.text+(item3.type=="group"||item3.type=="inlineGroup"?"":((item3.type=="table"?"":"")+""+item3.bindKey+"")))
                })
              })
            })
          })
          objectData.compositeAttrToc = moduleAttrList.join("\n");
          objectData.innerAttrDataToc = "";
          let innerAttrList = [];
          if(that.checkModuleObject.innerComName||that.checkModuleObject.innerAttr){
            innerAttrList.push("\n## ");
            innerAttrList.push(that.checkModuleObject.innerComName||"");
            innerAttrList.push("## ");
            that.checkModuleObject.innerAttr&&that.checkModuleObject.innerAttr.forEach(item=>{
              innerAttrList.push("### "+item.text+(item.type=="group"||item.type=="inlineGroup"?"":((item.type=="table"?"":"")+""+item.bindKey+"")))
              item.children&&item.children.forEach(item1=>{
                innerAttrList.push("#### "+item1.text+(item1.type=="group"||item1.type=="inlineGroup"?"":((item1.type=="table"?"":"")+""+item1.bindKey+"")))
                item1.children&&item1.children.forEach(item2=>{
                  innerAttrList.push("##### "+item2.text+(item2.type=="group"||item2.type=="inlineGroup"?"":((item2.type=="table"?"":"")+""+item2.bindKey+"")))
                  item2.children&&item2.children.forEach(item3=>{
                    innerAttrList.push("###### "+item3.text+(item3.type=="group"||item3.type=="inlineGroup"?"":((item3.type=="table"?"":"")+""+item3.bindKey+"")))
                  })
                })
              })
            })
            objectData.innerAttrDataToc = innerAttrList.join("\n");
          }

          that.editorText = that.$replace(that.moduleMDTemp, objectData);
        }
        setTimeout(() => {
          that.editorIsChange = false;
        }, 100);
      })
    },
    /**
     * Md
     */
    getMdContent(path,callback){
      IDM.http.get(path,{},{responseType:'text'}).then(res=>{
        // console.log(res);
        callback&&callback(res);
      })
      .catch(function (error) {
        callback&&callback();
      })
    },
    //md
    mdEditorChange(text, html){
      console.log(" ~ file: Document.vue ~ line 238 ~ mdEditorChange ~ text", text)
      this.editorIsChange = true;
    },
    /**
     * md
     */
    saveMdEditorContent(){
      var params={
        mdPath:this.getModuleAbsoluteDir(true),
        type:"",
        moduleClassName:"",
        mdContent:this.editorText
      }
      //
      if(this.checkModuleObject.classId){
        //
        params.type="module";
        params.moduleClassName = this.checkModuleObject.className;
        params.mdPath = params.mdPath+this.checkVersionObject.codePath+IDM.setting.document.moduleDocumentPath;
      }else if(this.checkVersionObject.className){
        //
        params.type="code";
        params.moduleClassName = "index";
        params.mdPath = params.mdPath+this.checkVersionObject.codePath+IDM.setting.document.codepackageDocumentPath;
      }
      if(!IDM.setting.document.saveMarkDownFileUrl){
          IDM.message.info("");
          return;
      }
      this.$documentApi.saveMdEditorData(params)
      .then(res=>{
        IDM.message.success("");
      })
      .catch(function (error) {
        console.log(error);
      });

    },
    /**
     * download
     */
    downloadMdFile(){
      if(!this.checkModuleObject.classId){
        //
        window.open(IDM.url.getWebPath(this.getModuleAbsoluteDir()+this.checkVersionObject.codePath+IDM.setting.document.codepackageDocumentPath+"index.md"));
      }else{
        //
        window.open(IDM.url.getWebPath(this.getModuleAbsoluteDir()+this.checkVersionObject.codePath+IDM.setting.document.moduleDocumentPath+this.checkModuleObject.className+".md"));
      }
    },
    /**
     * 
     * isSavetruefalse
     */
    getModuleAbsoluteDir(isSave){
      if(!isSave&&process.env.NODE_ENV == "development"){
        //
        return "@";
      }
      return IDM.setting.webRoot.moduleAbsoluteDir.replace(/{projectNo}/g, this.checkVersionObject.projectNo||IDM.setting.webRoot.projectNo);
    }
  },
  watch:{
    checkVersionObject:{
      handler(newVal, oldVal) {
        if(!this.checkVersionObject.configObject){
          this.checkVersionObject.configObject = JSON.parse(this.checkVersionObject.configText)
        }
      },
    }
  }
};
</script>
<style lang="scss">
@import "../style/core/document.scss";
</style>